<template>
  <div>
    <van-nav-bar title="我的" class="bgc"></van-nav-bar>
    <div class="content">
      <div class="my-box">
        <div class="my-card clearfix" :style="{backgroundImage: 'url(' + user.userBg + ')'}">
          <div class="update fl">
            <!-- 上传头像 -->
            <van-image width="100" height="100" :src="user.userImg" />
          </div>
          <div class="fl">
            <p class="name">{{user.nickName}}</p>
          </div>
          <div class="changebg">
            <div class="fr cg">
              <div v-if="code == 'A001'" style="color:#ccc">
                更改背景
                <van-uploader class="upload-box" :max-size="maxSize" :after-read="uploadFile" />
              </div>
              <div v-else @click="goLogin">请先登录</div>
            </div>
          </div>
        </div>

        <div class="order">
          <van-grid clickable :column-num="3">
            <van-grid-item @click="goTo({name:'Myorder',params:{status:1}})">
              <span class="iconfont icon-shouhuo"></span>
              <p>待收货</p>
            </van-grid-item>

            <van-grid-item :to="{name:'Mylike'}">
              <span class="iconfont icon-shoucang"></span>
              <p>我的收藏</p>
            </van-grid-item>

            <van-grid-item @click="goTo({name:'Myorder',params:{status:2}})">
              <span class="iconfont icon-daifukuan"></span>
              <p>已收货</p>
            </van-grid-item>
          </van-grid>
        </div>

        <div class="my-items">
          <div
            class="my-list clearfix"
            v-for="(item,index) in myItems"
            :key="index"
            @click="goTo({name:item.name})"
          >
            <div class="fl">
              <span :class="['iconfont', item.icon]">
                <span class="list-title">{{item.title}}</span>
              </span>
            </div>
            <div class="fr go-icon">
              <van-icon name="arrow" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/my.less";
import "@/assets/iconfont/font/iconfont.css";

export default {
  data() {
    return {
      maxSize: 1 * 1024 * 1024,
      code: "",
      imgName: "",
      // 列表数据
      myItems: [
        {
          title: "我的订单",
          icon: "icon-weibiaoti-",
          name: "Myorder"
        },

        {
          title: "我的设置",
          icon: "icon-wodeshezhi",
          name: "Myset"
        },

        {
          title: "个人信息",
          icon: "icon-wodeshezhi",
          name: "Myinfo"
        }
      ],
      user: {},

      isChangeName: true,
      name: "请输入名字",

      // 背景图片更换
      bgImg: "url('img/mybgImg.jpg') ",

      show: false
    };
  },

  created() {
    this.getuser();
  },

  methods: {
    my() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == "A001") {
            this.$toast(res.data.msg);
          }
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //获取用户信息
    getuser() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          this.code = result.data.code;
          if (result.data.code == "A001") {
            this.user = result.data.result[0];
          } else if (result.data.code == "700") {
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //获取上传文件的base64
    uploadFile(file) {
      

      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "I001") {
            this.user.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 跳转页面
    goTo(routname) {
      this.$router.push(routname);
    },

    goLogin() {
      this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style lang="less" scoped>
</style>